@import '../../styles/_variables.scss';
@import '../../styles/_mixins.scss';

.card {
  position: relative;
  box-sizing: border-box;
  min-height: 80px;
  height: auto;
  border-radius: 4px;
  margin: 0 8px 8px;
  background-color: $white;
  box-shadow: 0 1px 3px 0 rgba(21, 27, 38, 0.15);
  .card__avatar {
    width: 20px;
    height: 20px;
    line-height: 20px;
    &:not(:last-child) {
      margin-right: 4px;
    }
  }
  .card__avatar-placeholder {
    font-size: 9px;
    font-weight: 600;
  }
}

.card__title {
  font-size: 14px;
  margin: 0;
  font-weight: 400;
  line-height: 1.3;
  color: $grey-900;
}

.card__header {
  position: relative;
  padding: 8px 10px 6px 10px;
}

.card__body {
  padding: 0 10px 0 10px;
  font-size: 12px;
  line-height: 1;
}

.card__tag {
  margin-bottom: 4px;
}

.card__tags {
  width: 100%;
  display: flex;
  margin-bottom: 4px;
}

.card__detail {
  font-size: 11px;
  line-height: 1;
  display: inline-block;
  color: $grey-700;
  .feather {
    width: 12px;
    height: 12px;
    vertical-align: bottom;
    margin-right: 4px;
  }
  &:not(:first-child) {
    margin-left: 8px;
  }
  &.card__due-date {
    font-weight: 600;
    color: $grey-900;
  }
  &.due--today {
    color: $secondary;
  }
  &.due--tmrw {
    color: $primary;
  }
  &.due--yest {
    color: $red;
  }
}

.card__members {
  display: flex;
  padding: 0 10px 6px 10px;
  align-items: center;
  justify-content: flex-end;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.card__members-wrapper {
  height: 32px;
}
